<template>
  <div class="updatePassword">
    <el-form
      ref="updatePasswordForm"
      label-width="170px"
      label-position="bottom"
      autocomplete="on"
      :model="updatePasswordForm"
      :rules="updatePasswordFormRules"
    >
      <el-form-item label="旧密码" prop="oldPassword">
        <el-input
          v-model="updatePasswordForm.oldPassword"
          type="password"
          ref="oldPassword"
          placeholder="旧密码"
          name="oldPassword"
          tabindex="1"
          autocomplete="on"
        ></el-input>
      </el-form-item>
      <el-form-item label="新密码" prop="newPassword">
        <el-input
          v-model="updatePasswordForm.newPassword"
          type="password"
          ref="newPassword"
          placeholder="新密码"
          name="newPassword"
          tabindex="2"
          autocomplete="on"
        ></el-input>
      </el-form-item>
      <el-form-item label="确认密码" prop="confirmPassword">
        <el-input
          v-model="updatePasswordForm.confirmPassword"
          type="password"
          ref="confirmPassword"
          placeholder="确认密码"
          name="confirmPassword"
          tabindex="3"
          autocomplete="on"
        ></el-input>
      </el-form-item>
      <el-row>
        <el-col :span="24">
          <div class="button-body">
            <div class="doConfirm doButton">
              <el-button type="primary" @click="doConfirm('updatePasswordForm')">确认</el-button>
            </div>
            <div class="doConfirm doButton">
              <el-button type="primary" @click="doReset('updatePasswordForm')">重置</el-button>
            </div>
            <div class="doConfirm doButton">
              <el-button type="primary" @click="doBack">返回</el-button>
            </div>
          </div>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>
<script>
import { updatePasswordAction } from '@/api/user'
import { Message } from 'element-ui'
export default {
  name: "UpdatePassword",
  data() {
    // rule, value, callback
    const confirmPasswordMake = (rule, value, callback) => {
      if (value !== this.updatePasswordForm.newPassword) {
        callback(new Error('两次输入的密码不一致，请重新输入！'))
      } else {
        callback()
      }
    };
    return {
      updatePasswordForm: {
        oldPassword: "",
        newPassword: "",
        confirmPassword: ""
      },
      updatePasswordFormRules: {
        oldPassword: [
          {
            required: true,
            trigger: "blur",
            message: "密码不能为空！"
          }
        ],
        newPassword: [
          {
            required: true,
            trigger: "blur",
            message: "密码必须由数字、下划线、字母组成!"
          }
        ],
        confirmPassword: [
          {
            required: true,
            trigger: "blur",
            validator: confirmPasswordMake
          }
        ]
      }
    };
  },
  methods: {
    doConfirm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          debugger
          const { userName } = JSON.parse(sessionStorage.getItem('data'))
          updatePasswordAction({
            oldPassWord: this.updatePasswordForm.oldPassword,
            passWord: this.updatePasswordForm.newPassword,
            userName: userName
          }).then(async resp => {
            if (resp.status === 200) {
              Message({
                  message: "密码修改成功!",
                  type: "success",
                  duration: 3 * 1000
                });
              await this.$store.dispatch("user/doLoginOut");
              this.$router.push({ path: '/login' })
            }
          })
        }
      })
    },
    doReset(formName) {
      this.$refs[formName].resetFields();
    },
    doBack() {
      this.$router.go(-1);
    }
  }
};
</script>
<style lang="scss" scoped>
.updatePassword {
  width: 50%;
  margin: 52px auto;
  .button-body {
      text-align: right;
  }
  .doButton {
    margin: 0 30px;
    display: inline-block;
  }
}
</style>